* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: linear-gradient(45deg, rgb(118, 201, 255), rgb(247, 255, 222));
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.left-mountain {
  width: 300px;
  height: 300px;
  background: linear-gradient(rgb(203, 241, 228), rgb(80, 183, 255));
  position: absolute;
  transform: skew(0deg, 44deg);
  z-index: 2;
  margin-top: 100px;
}
.back-mountain {
  width: 300px;
  height: 300px;
  background: linear-gradient(rgb(203, 241, 228), rgb(80, 183, 255));
  position: absolute;
  left: 110px;
  top: 225px;
  z-index: 1;
  transform: rotate(45deg);
}
.sun {
  width: 200px;
  height: 200px;
  background-color: yellow;
  position: absolute;
  top: -75px;
  right: -75px;
  border-radius: 50%;
}

.penguin {
  width: 300px;
  height: 300px;
  margin: auto;
  margin-top: 75px;
  position: relative;
  z-index: 4;
  .penguin-head {
    width: 50%;
    height: 45%;
    background: linear-gradient(45deg, gray, rgb(239, 240, 228));
    border-radius: 70% 70% 65% 65%;
  }
  .penguin-body {
    width: 53%;
    height: 45%;
    background: linear-gradient(
      45deg,
      rgb(134, 133, 133) 0%,
      rgb(234, 231, 231) 25%,
      white 67%
    );
    border-radius: 70% 70% 65% 65%;
  }
}

.ground {
  width: 100vw;
  height: 400px;
  background: linear-gradient(90deg, rgb(88, 175, 236), rgb(182, 255, 255));
  z-index: 3;
  position: absolute;
  margin-top: -58px;
}
